"use client";
import { useState } from "react";
import Link from "next/link";

export default function DocsSidebar() {
  const [activeSection, setActiveSection] = useState("getting-started");
  const [expandedSections, setExpandedSections] = useState(["getting-started", "uploading"]);
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

  const navigationSections = [
    {
      id: "getting-started",
      title: "Getting Started",
      icon: "unicon-rocket",
      subsections: [
        { id: "overview", title: "Platform Overview", href: "#overview" },
        { id: "account-setup", title: "Account Setup", href: "#account-setup" },
        { id: "first-project", title: "Your First Project", href: "#first-project" },
        { id: "supported-formats", title: "Supported Formats", href: "#supported-formats" }
      ]
    },
    {
      id: "uploading",
      title: "Upload & Import",
      icon: "unicon-upload",
      subsections: [
        { id: "upload-methods", title: "Upload Methods", href: "#upload-methods" },
        { id: "file-formats", title: "File Formats", href: "#file-formats" },
        { id: "coordinate-systems", title: "Coordinate Systems", href: "#coordinate-systems" },
        { id: "large-files", title: "Large File Handling", href: "#large-files" }
      ]
    },
    {
      id: "visualization",
      title: "Visualization",
      icon: "unicon-eye",
      subsections: [
        { id: "viewer-controls", title: "Viewer Controls", href: "#viewer-controls" },
        { id: "rendering-options", title: "Rendering Options", href: "#rendering-options" },
        { id: "view-modes", title: "View Modes", href: "#view-modes" },
        { id: "annotations", title: "Annotations", href: "#annotations" }
      ]
    },
    {
      id: "measurements",
      title: "Measurements",
      icon: "unicon-ruler",
      subsections: [
        { id: "distance-tools", title: "Distance Tools", href: "#distance-tools" },
        { id: "area-volume", title: "Area & Volume", href: "#area-volume" },
        { id: "custom-measurements", title: "Custom Measurements", href: "#custom-measurements" },
        { id: "export-measurements", title: "Export Measurements", href: "#export-measurements" }
      ]
    },
    {
      id: "collaboration",
      title: "Collaboration",
      icon: "unicon-users",
      subsections: [
        { id: "sharing-projects", title: "Sharing Projects", href: "#sharing-projects" },
        { id: "user-permissions", title: "User Permissions", href: "#user-permissions" },
        { id: "comments-reviews", title: "Comments & Reviews", href: "#comments-reviews" },
        { id: "version-control", title: "Version Control", href: "#version-control" }
      ]
    },
    {
      id: "security",
      title: "Security & Privacy",
      icon: "unicon-shield",
      subsections: [
        { id: "data-security", title: "Data Security", href: "#data-security" },
        { id: "access-control", title: "Access Control", href: "#access-control" },
        { id: "compliance", title: "Compliance", href: "#compliance" },
        { id: "backup-recovery", title: "Backup & Recovery", href: "#backup-recovery" }
      ]
    },
    {
      id: "api",
      title: "API Reference",
      icon: "unicon-code",
      subsections: [
        { id: "authentication", title: "Authentication", href: "#authentication" },
        { id: "endpoints", title: "API Endpoints", href: "#endpoints" },
        { id: "webhooks", title: "Webhooks", href: "#webhooks" },
        { id: "rate-limits", title: "Rate Limits", href: "#rate-limits" }
      ]
    },
    {
      id: "troubleshooting",
      title: "Troubleshooting",
      icon: "unicon-life-ring",
      subsections: [
        { id: "common-issues", title: "Common Issues", href: "#common-issues" },
        { id: "error-codes", title: "Error Codes", href: "#error-codes" },
        { id: "performance", title: "Performance Tips", href: "#performance" },
        { id: "contact-support", title: "Contact Support", href: "#contact-support" }
      ]
    }
  ];

  const toggleSection = (sectionId) => {
    setExpandedSections(prev => 
      prev.includes(sectionId) 
        ? prev.filter(id => id !== sectionId)
        : [...prev, sectionId]
    );
  };

  const SidebarContent = () => (
    <div className="bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700 rounded-3 p-4">
      <div className="hstack gap-3 mb-4">
        <h6 className="fs-6 fw-bold text-dark dark:text-white mb-0">
          Documentation
        </h6>
        <button
          className="btn btn-sm btn-outline-light ms-auto d-lg-none"
          onClick={() => setIsMobileMenuOpen(false)}
        >
          <i className="unicon-times"></i>
        </button>
      </div>
      
      <nav className="docs-nav">
        {navigationSections.map((section) => (
          <div key={section.id} className="nav-section mb-3">
            <button
              className={`nav-section-header btn btn-sm w-100 text-start p-2 rounded-2 border-0 ${
                expandedSections.includes(section.id) 
                  ? "bg-primary bg-opacity-10 text-primary" 
                  : "bg-transparent text-dark dark:text-white"
              }`}
              onClick={() => toggleSection(section.id)}
            >
              <div className="hstack gap-3">
                <i className={`${section.icon} fs-7`}></i>
                <span className="flex-grow-1 fs-7 fw-medium">{section.title}</span>
                <i className={`unicon-chevron-down fs-8 transition-transform ${
                  expandedSections.includes(section.id) ? "rotate-180" : ""
                }`}></i>
              </div>
            </button>
            
            {expandedSections.includes(section.id) && (
              <div className="nav-subsections mt-2 ms-3">
                {section.subsections.map((subsection) => (
                  <Link
                    key={subsection.id}
                    href={subsection.href}
                    className={`nav-subsection-link btn btn-sm w-100 text-start p-2 rounded-2 border-0 ${
                      activeSection === subsection.id 
                        ? "bg-primary bg-opacity-5 text-primary border-start border-primary border-3" 
                        : "text-dark dark:text-white text-opacity-70 hover:text-primary"
                    }`}
                    onClick={() => {
                      setActiveSection(subsection.id);
                      setIsMobileMenuOpen(false);
                    }}
                  >
                    <span className="fs-8">{subsection.title}</span>
                  </Link>
                ))}
              </div>
            )}
          </div>
        ))}
      </nav>

      {/* Help Section */}
      <div className="help-section mt-6 pt-4 border-top border-gray-200 dark:border-gray-700">
        <h6 className="fs-7 fw-medium text-dark dark:text-white mb-3">
          Need Help?
        </h6>
        <div className="vstack gap-2">
          <Link href="/ressources/support" className="btn btn-sm btn-outline-primary w-100">
            <i className="unicon-life-ring me-2"></i>
            Support Center
          </Link>
          <Link href="/ressources/blog" className="btn btn-sm btn-outline-light w-100">
            <i className="unicon-book me-2"></i>
            Tutorials
          </Link>
        </div>
      </div>
    </div>
  );

  return (
    <>
      {/* Mobile Menu Toggle Button */}
      <div className="d-lg-none mb-4">
        <button
          className="btn btn-outline-primary w-100"
          onClick={() => setIsMobileMenuOpen(true)}
        >
          <i className="unicon-menu me-2"></i>
          Browse Documentation
        </button>
      </div>

      {/* Desktop Sidebar */}
      <div className="docs-sidebar sticky-top d-none d-lg-block" style={{ top: "100px" }}>
        <SidebarContent />
      </div>

      {/* Mobile Sidebar Overlay */}
      {isMobileMenuOpen && (
        <div className="mobile-sidebar-overlay d-lg-none">
          <div className="position-fixed top-0 start-0 w-100 h-100 bg-black bg-opacity-50 z-10" onClick={() => setIsMobileMenuOpen(false)}></div>
          <div className="position-fixed top-0 start-0 w-100 h-100 z-20 overflow-auto">
            <div className="container-fluid p-4">
              <div className="row">
                <div className="col-12">
                  <SidebarContent />
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </>
  );
} 